.lists_fans li{
    list-style: none;
    width: 25%;
    text-align: center;
}
.lists_head{
    display: flex;
    align-items: center;
    padding:3vw 0 0 5vw;
}
.lists_head p:first-child{
    width: 15vw;
    height: 15vw;
}
.lists_head p:first-child img{
    width: 100%;
    height: 100%;
}
.lists_fans{
    display: flex;
    padding: 0;
    font-size: 4vw;
}
.head-text span{
    display: block;
    font-size: 3.5vw;
    color: gray;
}
.head-text span:first-child{
    font-weight: 700;
    font-size: 4.5vw;
    padding:  1vw 0 ;
    color: black;
}
.shezhi{
    list-style: none;
    padding-left: 5vw;
    font-size: 4vw;
}
.shezhi li{
    padding:  3vw 0;
}
.shezhi .iconfont{
    padding-right:5vw;
    font-size: 4vw;
}