body{
    overflow: auto;
    
}
.reg_login{
    width: 80vw;
    margin: 20vw auto;
    text-align: center;
    border: 1px solid gray;
}
.reg_login p{
    margin: 5vw 0;
    display: flex;
    align-items: center;
    justify-content: center;
}
.reg_login input{
    outline: none;
    border: 1px solid gray;
    padding:1vw 0 1vw 2vw;
    box-sizing: border-box;
    width: 40vw;
    height: 5vw;
    font-size: 3.5vw;
}
.reg_login input:focus{
    border: 1px solid red;
}
.reg_login input::placeholder{
    font-size: 3.5vw;
}
.reg_login p:first-child{
    font-size: 4.5vw;
}
label{
    font-size: 4vw;
    margin-right: 2vw;
}
.reg_login span{
    display: inline-block;
    padding: 1vw 3vw;
    border: 1px solid gray;
    margin: 1vw 6.5vw;
    font-size: 3.5vw;
}