.joke {
  padding: 0 5vw;
}
.joke_item {
  padding: 0 0 4vw 0;
  border-bottom: 0.2vw solid #cecece;
}
.jokeuser {
  display: flex;
  font-weight: 700;
  font-size: 3.5vw;
  align-items: center;
  padding: 3vw 0 2vw 0;
}
.joke_item p{
  font-size: 4vw !important;
}
.jokeuser .head {
  padding: 0;
  width: 8vw;
  height: 8vw;
  border-radius: 50%;
  overflow: hidden;
}
.head img {
  width: 100%;
  height: 100%;
}
.jokeuser .name {
  padding: 0 0 0 2vw;
}
.name {
  font-size: 3.5vw;
}
.icon-dianzan2 {
  transform: rotateX(180deg) !important;
}
.comment {
  display: flex;
  margin: 2vh  0;
}
.comment div {
  width: 25%;
  padding-left: 5vw;
  display: flex;
  align-items: center;
  color: gray;
  font-size: 3vw;
}
.comment span {
  padding-left: 1vw;
}
/* 热评背景 */
 .newComment{
  background: url(../assest/img/newcomment.png) no-repeat;
  background-size: 15vw;
  background-position: center right;
  padding: 5vw 0;
  color: gray;
  font-size: 3vw;
}
.newUser{
  display:flex;
  align-items: center;
  font-size: 3vw;
  color: gray;
}
.newUser img{
  width: 6vw;
  height: 6vw;
  border-radius: 50%;
}
.newUser span{
  padding-left:1vw ;
}

