.am-tabs-default-bar-tab {
  font-size: 3.5vw !important;
  height: 9vw;
}
.am-tabs-default-bar-tab span{
  font-size: 3.5vw;
}
.am-tabs-default-bar-tab-active span{
  font-size: 4.5vw;
}
.am-tabs-tab-bar-wrap{
  width: 100%;
}
.login{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
}
.login .icon-baisibudejie{
  margin: 0 ;
  font-size: 8vw;
  color: red;
  text-align: right;
}
.loginText{
  color: red;
  font-size: 8vw;
  margin: 0;
  font-family: '楷体';
}
.am-tabs-default-bar-tab-active {
  color: red !important;
  font-size: 4.5vw !important;
  box-sizing: content-box;
}
.am-tabs-default-bar-underline {
  position: absolute;
  border: 1px solid red !important;
  transform: translateZ(0);
}
/* 头部 */
.header-item {
  align-items: center;
  justify-content: center;
  background-color: #fff;
  height: 90vh;
  overflow: auto;
}
.header-item p{
    margin: 0;
    padding: 1vw 0;
}
.am-tabs-default-bar-top .am-tabs-default-bar-tab, .am-tabs-default-bar-bottom .am-tabs-default-bar-tab{
  padding: 0;
}