.ImageList{
    padding:  0 5vw;
}
.imageText p:first-child{
    width: 100%;
    height: auto;
}
.imageText{
    border-bottom:1px solid #cecece;
    font-size: 3.5vw;
}
.imageText img{
    width: 100%;
    height: 100%;
}
.ImageUser{
    padding: 2vh 0;
    display: flex;
    align-items: center;
    font-size: 4vw;
}
.ImageUser img{
    width: 8vw;
    height: 8vw;
    border-radius: 50%;
}
.ImageUser span{
    font-weight: 700;
    padding-left: 2vw;
}
/* 热评背景 */
.imageText .newComment{
    background: url(../assest/img/newcomment.png) no-repeat;
    background-size: 15vw;
    background-position: center right;
    padding: 5vw 0;
    color: gray;
    font-size: 3vw;
}
.newUser{
    display:flex;
    align-items: center;
    font-size: 3vw;
    color: gray;
}
.newUser img{
    width: 6vw;
    height: 6vw;
    border-radius: 50%;
}
.newUser span{
    padding-left:1vw ;
}
/* 点赞 */
.red{
    color: red !important;
}
.icon-dianzan1:before,.icon-xinxi:before{
    font-size: 4vw;
}
/* 踩 */
.cai::after{
    content: '踩';
    padding-left: 1vw;
}
.bad{
    color: tomato !important;
}
